nav {
  display: flex;
  width: 100%;
  top: 0;
  position: fixed;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  background-color: black;
  padding: 20px 60px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.6);
}

#navbarLinks {
  display: flex;
  align-items: center;
  justify-content: center;
}

#navbarLinks li {
  list-style: none;
  padding: 0 20px;
  position: relative;
}
#navbarLinks li a {
  text-decoration: none;
  font-size: 1.32rem;
  font-weight: 600;
  color: white;
  transition: 0.4s ease-in-out;
}

#navbarLinks li a:hover,
#navbarLinks li a.active {
  color: greenyellow;
}

#navbarLinks li a:hover::after,
#navbarLinks li a.active::after {
  content: "";
  width: 35%;
  height: 3px;
  background-color: greenyellow;
  position: absolute;
  bottom: -5px;
  left: 25px;
}

#mobile {
  display: none;
}
#mobile i {
  color: white;
}

@media screen and (max-width: 750px) {
  #navbarLinks {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    position: fixed;
    top: 84px;
    right: -300px;
    width: 200px;
    height: 100vh;
    background-color: red;
    box-shadow: 0 35px 50px rgba(0, 0, 0, 0.2);
    padding: 10px 30px 0px 3px;
    transition: 0.3s ease-in-out;
  }
  #navbarLinks.active {
    right: 0px;
  }
  #navbarLinks li {
    margin-bottom: 20px;
  }
  #mobile {
    display: block;
  }
  #mobile i {
    font-size: 25px;
    cursor: pointer;
  }
}
